export default function authenticationReducer(state = initialState, action) {
    switch (action.type) {
        case AUTHENTICATE:
            return {
                ...state,
                token: action.token,
                firstName: action.firstName,
                lastName: action.lastName,
                avatar: action.avatar,
                location: action.location,
            }
        default:
            return state
    }
}
